module.exports = ".fav-title {\n    color: #fff;\n    padding-top: 25px;\n}\n\n.del-icon:hover {\n    color: #48ff00;\n    text-shadow: 0 0 10px rgb(0, 0, 0);\n}\n\n.a {\n    grid-area: a;\n}\n\n.b {\n    grid-area: day;\n}\n\n.grid-container {\n    margin-top: 50px;\n    width: 60%;\n    margin-right: auto;\n    margin-left: auto;\n    background-color: #fff;\n    padding: 30px;\n    display: grid;\n    grid-template-areas: 'a a a a a';\n    grid-gap: 30px;\n    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);\n}\n\n.grid-container>div {\n    height: 300px;\n    padding-top: 50px;\n    font-size: 20px;\n    text-align: center;\n    background: rgb(255, 255, 255);\n    background: radial-gradient(circle, rgb(165, 225, 255) 0%, rgb(57, 189, 255) 150%);\n    display: grid;\n    grid-template-areas: 'b' 'b' 'b';\n    -webkit-transition: linear .15s;\n    transition: linear .15s;\n}\n\n.grid-container>div:hover {\n    background: radial-gradient(circle, #345579 0%, #1b1d35 150%);\n    cursor: pointer;\n    color: #fff;\n}\n\n@media (max-width:850px) {\n    .grid-container {\n        width: 95%;\n        grid-template-areas: 'a' 'a' 'a' 'a' 'a';\n    }\n    .grid-container>div {\n        padding-top: 10px;\n        height: 170px;\n        border-bottom: 1px solid rgb(204, 204, 204);\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"